import React, { useState, useEffect } from "react";
import style from "./Faq.module.scss";

import clsx from "clsx";

import { BsChevronRight } from "react-icons/bs";

import SectionHeader from "@components/SectionHeader";

export default function Faq({ faq }) {
  const [faqs, setFaqs] = useState([]);

  function toggleFaq(id) {
    let toUpdate = [...faqs];
    toUpdate.forEach((item) => {
      if (item.id === id) item.isOpen = !item.isOpen;
    });
    setFaqs(toUpdate);
  }

  useEffect(() => {
    setFaqs(faq.map((f) => ({ ...f, isOpen: false })));
  }, [faq]);

  return (
    <section className={style.wrapper}>
      <header>
        <SectionHeader>FAQs</SectionHeader>
      </header>

      <section className={style.faqs}>
        {faqs.map((item) => (
          <article onClick={toggleFaq.bind(this, item.id)}>
            <div className={style.trigger}>
              {item.question}{" "}
              <span className={clsx(item.isOpen && style.rotated)}>
                <BsChevronRight />
              </span>
            </div>
            <div className={clsx(style.collapsed, item.isOpen && style.open)}>{item.answer}</div>
          </article>
        ))}
      </section>
    </section>
  );
}
